export default (function() {
    window.setInterval(function(){
        const clock = document.querySelectorAll('#clock');
        const date = new Date();
        let hours = date.getHours();
        let minutes = date.getMinutes();

        if (hours < 10) {
            hours = "0" + hours;
        }
        
        if (minutes < 10) {
            minutes = "0" + minutes;
        }
        
        clock.forEach((item) => {
            item.innerHTML = hours + ":" + minutes;
        });
    }, 1000);
}());